import React from 'react';
import { HashRouter, Route, Link } from 'react-router-dom';
import Projects from './Projects.js';
import Art from './Art.js';
import About from './About.js';
import Comics from './Comics.js';
import Traditional from './Traditional.js';
import Digital from './Digital.js';
import Editorial from './Editorial.js';
import Auxilio from './Auxilio.js';
import Code from './Code.js';
import UXCase from './UXCase.js';
import Visual from './Visual.js';
import Sustainability from './Sustainability.js';
import Pennenv from './Pennenv.js';

import './App.css';

function App() {
  return (
    <HashRouter>
      <div className="App">
      <div className="header">
        <div className="navigation">
        <h1>Connie Liu Portfolio</h1>
            <Link to="/" className="item">Projects</Link>
            <Link to="/art" className="item">Art</Link>
            <Link to="/about" className="item">About</Link>
            <div className="navigation-sub">
          </div>
          <Route exact path="/" component={Projects} />
          <Route path="/art" component={Art} />
          <Route path="/about" component={About} />
        </div>
        </div>
        <Route path="/comics" component={Comics} />
        <Route path="/traditional" component={Traditional} />
        <Route path="/digital" component={Digital} />
        <Route path="/editorial" component={Editorial} />
        <Route path="/auxilio" component={Auxilio} />
        <Route path="/visual" component={Visual} />
        <Route path="/code" component={Code} />
        <Route path="/UXcase" component={UXCase} />
        <Route path="/sustainability" component={Sustainability} />
        <Route path="/pennenv" component={Pennenv} />

      </div>
    </HashRouter>
  );
}

export default App;
